.systemInfo {
  width: 100%;
  .title {
    font-size: 20px;
    font-weight: 600;
  }
  .text {
    height: 26px;
    line-height: 26px;
  }
  .badge {
    :global(.ant-badge-status-dot) {
      width: 10px;
      height: 10px;
    }
  }
  .error {
    color: red;
  }
}

@hack: true; @import "~@/less/index.less";